.TeamSearchForm {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.TeamSearchForm-input {
  border: none;
  border-bottom: 1px solid rgb(238, 238, 238);
  outline: none;
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 28px;
  padding: 4px 8px;
}

.TeamSearchResults-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.TeamSearchResults-item {
  border-bottom: 1px solid rgb(238, 238, 238);
  padding: 16px 8px;
}

.TeamSearchResults-item:hover {
  background: rgb(238, 238, 238);
}

.TeamSearchResult-name {
  font-weight: bold;
}

.TeamSearchResult {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.TeamSearchResult-view-games {
  background: #666;
  border-radius: 3px;
  color: rgb(238, 238, 238);
  padding: 8px 16px;
  text-decoration: none;
}



